export default class FormValidator {
  constructor(config, form) {
    this._formSelector = config.formSelector;
    this._inputSelector = config.inputSelector;
    this._submitButtonSelector = config.submitButtonSelector;
    this._inactiveButtonClass = config.inactiveButtonClass;
    this._inputErrorClass = config.inputErrorClass;
    this._errorClass = config.errorClass;
    this._formElement = form;
  }

  // Ð½Ð°ÑÐ¾Ð´Ð¸Ð¼ Ð²ÑÐµ ÑÐ¾ÑÐ¼Ñ Ð² Ð¼Ð°ÑÑÐ¸Ð²
  enableValidation() {
    this._makeSubmitEvtListener();
    this._setInputListener()
  }

  // Ð½Ð°Ð²ÐµÑÐ¸Ð²Ð°ÐµÐ¼ ÑÐ»ÑÑÐ°ÑÐµÐ»Ð¸ Ð½Ð° Ð·Ð°Ð¿ÑÐµÑ Ð¾ÑÐ¿ÑÐ°Ð²ÐºÐ¸ ÑÐ¾ÑÐ¼Ñ
  _makeSubmitEvtListener() {
    this._formElement.addEventListener('submit', this._preventSubmitForm)
  }

  // Ð¾ÑÐ¼ÐµÐ½ÑÐµÐ¼ Ð¾ÑÐ¿ÑÐ°Ð²ÐºÑ ÑÐ¾ÑÐ¼Ñ
  _preventSubmitForm(e) {
    e.preventDefault();
  }

  // Ð½Ð°ÑÐ¾Ð´Ð¸Ð¼ Ð²ÑÐµ Ð¿Ð¾Ð»Ñ Ð²Ð²Ð¾Ð´Ð°
  _setInputListener() {
    this._inputs = Array.from(this._formElement.querySelectorAll(this._inputSelector));
    this._button = this._formElement.querySelector(this._submitButtonSelector);
    this._inputs.forEach(input => {
       this._makeInputEvtListener(input)
       })
  }

  // Ð½Ð°Ð²ÐµÑÐ¸Ð²Ð°ÐµÐ¼ ÑÐ»ÑÑÐ°ÑÐµÐ»Ñ Ð½Ð° ÐºÐ°Ð¶Ð´ÑÐ¹ Ð²Ð²Ð¾Ð´ Ð² Ð¸Ð½Ð¿ÑÑ
  _makeInputEvtListener(input) {
    input.addEventListener('input', e => {
      this._inputValidity(input);
      //Ð¾Ð¿ÑÐµÐ´ÐµÐ»ÑÐµÑ ÑÐ¾ÑÑÐ¾ÑÐ½Ð¸Ðµ ÐºÐ½Ð¾Ð¿ÐºÐ¸
      this._toggleButtonState();
    })
  }

  // ÐÐ¿ÐµÑÐµÐ´ÐµÐ»ÑÐµÐ¼ ÑÐ´Ð°Ð»ÑÑÑ ÑÐµÐºÑÑ Ð¾Ð± Ð¾ÑÐ¸Ð±ÐºÐµ Ð¸ Ð¿Ð¾Ð´ÑÐµÑÐºÐ¸Ð²Ð°Ð½Ð¸Ðµ Ð¸Ð»Ð¸ Ð½ÐµÑ
  _inputValidity(input) {
    if (input.validity.valid) {
      this._hideInputError(input)
    } else {
      this._showInputError(input)
    }
  }

  // ÑÐ´Ð°Ð»ÐµÐ½Ð¸Ðµ ÑÐµÐºÑÑÐ° Ð¾Ð± Ð¾ÑÐ¸Ð±ÐºÐµ
  _hideInputError(input) {
    const inputError = this._formElement.querySelector(`#${input.id}--error`);
    input.classList.remove(this._inputErrorClass);
    inputError.textContent = '';
    inputError.classList.remove(this._errorClass)
  }

  // Ð´Ð¾Ð±Ð°Ð²Ð»ÐµÐ½Ð¸Ðµ ÑÐµÐºÑÑÐ° Ð¾Ð± Ð¾ÑÐ¸Ð±ÐºÐµ
  _showInputError(input) {
    const inputError = this._formElement.querySelector(`#${input.id}--error`);
    input.classList.add(this._inputErrorClass);
    inputError.textContent = input.validationMessage;
    inputError.classList.add(this._errorClass);
  }
  // ÑÐ±ÑÐ°ÑÑÐ²Ð°ÐµÐ¼ Ð¾ÑÐ¸Ð±ÐºÐ¸ Ð¸Ð½Ð¿ÑÑÐ¾Ð² Ð¿ÑÐ¸ Ð¿Ð¾Ð²ÑÐ¾ÑÐ½Ð¾Ð¼ Ð¾ÑÐºÑÑÑÐ¸Ð¸.
  // Ð¿ÑÐ¸ÑÐ»Ð¾ÑÑ ÐµÐµ ÑÐ´ÐµÐ»Ð°ÑÑ Ð¸ Ð¾Ð±ÑÐ°ÑÐ¸ÑÑÑ Ðº Ð½ÐµÐ¹ Ð¿ÑÐ¸ Ð¾ÑÐºÑÑÑÐ¸Ð¸ ÑÐ¾ÑÐ¼Ñ ÐºÐ°ÑÑÐ¾ÑÐºÐ¸ Ð¸ Ð¿ÑÐ¾ÑÐ¸Ð»Ñ Ð´Ð»Ñ ÑÐ±ÑÐ¾ÑÐ° Ð¿ÑÐµÐ´ÑÐ´ÑÑÐ¸Ñ Ð¾ÑÐ¸Ð±Ð¾Ðº
  resetFormState() {
    this._inputs.forEach (inputElement => {
      this._hideInputError(inputElement)
    });
    this._toggleButtonState()
    

}

  // Ð¾Ð¿ÑÐµÐ´ÐµÐ»ÑÐµÐ¼ ÑÐ¾ÑÑÐ¾ÑÐ½Ð¸Ðµ ÐºÐ½Ð¾Ð¿ÐºÐ¸ Ð² Ð·Ð°Ð²Ð¸ÑÐ¸Ð¼Ð¾ÑÑÐ¸ Ð¾Ñ Ð²Ð°Ð»Ð¸Ð´Ð½Ð¾ÑÑÐ¸ Ð¸Ð½Ð¿ÑÑÐ¾Ð²
  _toggleButtonState() {
    if (!this._hasInvalidInputs()) {
      this._button.classList.add(this._inactiveButtonClass);
      this._button.disabled = true;
    } else {
      this._button.classList.remove(this._inactiveButtonClass);
      this._button.disabled = false;
    }
  }

  // Ð¿ÑÐ¾Ð²ÐµÑÑÐµÐ¼ Ð²Ð°Ð»Ð¸Ð´Ð½Ð¾ÑÑÑ Ð¸Ð½Ð¿ÑÑÐ¾Ð²
  _hasInvalidInputs() {
    const result = this._inputs.every(input => input.validity.valid)
    return result
  }  
}
