export default class Section {
  //Ð¿ÐµÑÐµÐ´Ð°ÐµÐ¼ Ð¼Ð°ÑÑÐ¸Ð² ÐºÐ°ÑÑÐ¾ÑÐµÐº, ÑÑÐ½ÐºÑÐ¸Ñ, Ð¸ ÑÐµÐ»ÐµÐºÑÐ¾Ñ ÑÐµÐºÑÐ¸Ð¸ ÐºÑÐ´Ð° Ð±ÑÐ´ÐµÐ¼ Ð´Ð¾Ð±Ð°Ð²Ð»ÑÑÑ Ð¸Ñ
  constructor({rendererItem}, containerSelector) {
    this._containerSelector = containerSelector;
    this._containerSection = document.querySelector(this._containerSelector)
    this._rendererItem = rendererItem

  }
// Ð¼ÐµÑÐ¾Ð´ Ð¾Ð±ÑÐ¾Ð´Ð¸Ñ ÐºÐ°ÑÑÐ¾ÑÐºÐ¸ Ð¸ Ð²ÑÐ·ÑÐ²Ð°ÐµÑ Ð¼ÐµÑÐ¾Ð´ Ð´Ð¾Ð±Ð°Ð²Ð»ÐµÐ½Ð¸Ñ Ð² ÑÐ°Ð·Ð¼ÐµÑÐºÑ
  renderer(dataArray, userId) {
    dataArray.reverse().forEach( item => this._rendererItem(item, userId))
  };
 
// Ð¼ÐµÑÐ¾Ð´ Ð´Ð¾Ð±Ð°Ð²Ð»ÐµÐ½Ð¸Ñ ÐºÐ°ÑÑÐ¾ÑÐºÐ¸ ÑÐµÑÐµÐ· ÑÐ¾ÑÐ¼Ñ
  addItem(element) {
    this._containerSection.prepend(element);
  }
}